
import { defineComponent, h } from 'vue'

  export default defineComponent({
    name: 'flip',
    props: {
      activeClick: {
        type: Boolean,
        required: false,
        default: false
      },
      activeHover: {
        type: Boolean,
        required: false,
        default: false
      },
      width: {
        type: String,
        required: true
      },
      height: {
        type: String,
        required: true
      },
      transition: {
        type: String,
        required: false,
        default: '0.5s'
      },
      modelValue: {
        type: Boolean,
        required: false
      }
    },
    render () {
      return h(
        'div',
        {
          class: `flip-container ${this.activeHover ? 'active-hover ' : ''}${this.hover ? 'hover' : ''}`,
          style: `width: ${this.width}; height: ${this.height}`,
          onClick: this.handleClick,
          onMouseOver: this.handleMouseover,
          onMouseOut: this.handleMouseout,
        },
        h(
          'div', {
            class: 'flipper',
            style: `transition: ${this.transition}`
          },
          [
            h(
              'div',
              {
                class: 'front'
              },
              this.$slots.front ? this.$slots.front() : ''
            ),
            h(
              'div',
              {
                class: 'back'
              },
              this.$slots.back ? this.$slots.back() : ''
            )
          ]
        )
      )
    },
    data () {
      return {
        hover: false
      }
    },
    methods: {
      handleClick () {
        if (this.activeClick) {
          this.hover = !this.hover
          this.$emit('update:modelValue', this.hover)
        }
      },
      handleMouseout () {
        if (this.activeHover) {
          this.$emit('update:modelValue', false)
        }
      },
      handleMouseover () {
        if (this.activeHover) {
          this.$emit('update:modelValue', true)
        }
      }
    },
    mounted () {
      if (this.modelValue) {
        this.hover = this.modelValue
      }
    },
    watch: {
      modelValue (value) {
        this.hover = value
      }
    }
  })
